//Layout
@import "layout";
@import "nutrition_label";

.recipe-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.boorecipe-recipe {

  .recipe-head {
    padding-bottom: $_recipe_container_padding;
  }

  .recipe-section-heading {
    margin-bottom: 0.5em;
  }

  .recipe-img-cont {
    margin-bottom: $_recipe_img_cont_margin_bottom;
  }

  .recipe-img {
    width: 100%;
    max-width: 100%;
  }

  .recipe-ingredients {

    margin-bottom: 1em;
    //background-color: #f5f5f5;
  }

  .recipe-meta {
    > div {
      padding: $_recipe_sections_padding
    }
  }

  //.recipe-time-info{
  //  padding: 1em 0em 0em;
  //
  //  text-align: center;
  //  >div{
  //    flex: auto;
  //  }
  //}

  @media screen and (min-width: 30em) {
    // When the Nutrition is side-by-side
    &.ingredient-side {
      .recipe-meta {
        flex: 1 1 50%;
        padding-right: 1em;
      }
      .recipe-ingredients {
        padding: $_recipe_ingredients_padding;
        flex: 1 0 40%;
      }
    }

    &.nutrition-side {
      .recipe-instructions {
        flex: 1 1 50%;
        padding-right: 1em;
      }
      .recipe-nutrition {
        flex: 1 0 40%;
      }
    }

    .recipe-additional-notes {
      grid-column: 1/3 span;
    }
  }

  div#comments {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-start;
    flex: 1 0 100%;
  }

}

.posttype-wrapper {

  main#posttype-main {
    padding: 1em;
  }

  /* Side bar Styling */
  #secondary.widget-area {
    padding: 1em;

    > li {
      list-style: none;
    }
  }
}

.recipe-author-name-box {
  display: flex;
  margin-right: 1em;

  .recipe-author-avatar {
    img {
      flex: 1;
      display: flex;
      margin-right: 0.25em
    }
  }
}


@media only screen and (min-width:50em){
  .recipe-layout-style1{
    .recipe-body .recipe-main-body{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: max-content;
      grid-gap: 1.5em 1em;
      margin-top: 1em;
      margin-bottom: 1em;

      /* h3:not(.nutrition-facts__title){
        background: #e8e8e8;
        line-height: 1.3em;
        padding-left: 0.5em;
        padding-right: 0.5em;
        font-weight: 400;
        letter-spacing: .05em;
        text-transform: uppercase;
      }*/

    }

    .recipe-instructions {
      grid-column: 1 / 2 span;
      grid-row: 1;

      .instructions-list {
        padding-left: 5px;
      }

      .recipe-single-instruction{
        &:before{
          border-radius: 0px;
          background-color: #35b518;
          color: #ffffff;
        }
        /*
        span {
          background: #35b51812;
          padding: 10px;
          flex-basis: 100%;
        }
        */
      }
    }

    .recipe-ingredients{
      //grid-column: 1 / 2 span;
      //grid-row: 2;
      .select-item {
        padding-left:0px;
        margin-right: 0px;
        margin-left: 30px;
        &:before {
          position: absolute;
          left: -25px;
          top: 5px;
          border: 2px solid #35b518;
        }
     }
    }

    .recipe-nutrition {
      grid-row: 1 / 2 span;
    }
  }


}


@import "video_responsive";
